@font-face {
    font-family: Roboto;
    src: url('../fonts/Roboto-Regular.ttf')
}
@font-face {
    font-family: RobotoCondensed;
    src: url('../fonts/RobotoCondensed-Regular.ttf')
}
@font-face {
    font-family: RobotoMedium;
    src: url("../fonts/Roboto-Medium.ttf")
}
@font-face {
    font-family: HindRegular;
    src: url("../fonts/Hind-Regular.ttf")
}
@font-face {
    font-family: HindLight;
    src: url("../fonts/Hind-Light.ttf")
}
:root {
    --scrollWidth: 8px;
    --launcherPadding: 10px;
    --launcherWidth: 350px;
    --appsPerRow: 3;
    --appIconPadding: 5;
    --launcherPaneWidth: #{"(var(--launcherWidth) - (2 * var(--launcherPadding)) - var(--scrollWidth))"};
    /*--launcherPaneWidth: (var(--launcherWidth));*/
    --itemWidth: (var(--launcherPaneWidth) / var(--appsPerRow));
    --itemNameHeight: #{"(0.3 * var(--itemWidth))"};
    --containerHeight: 75px;
}

* {
    box-sizing: border-box;
}
.hidden {
    display: none;
}

.container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
body {
    line-height: 1;
    margin: 0;
    color: #404040;
    width: var(--launcherWidth);
    padding: var(--launcherPadding);
    //min-height: 110px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;



}

ul.apps-list {
    display: flex;
    align-content: flex-start;
    flex-wrap: wrap;
    list-style-type: none;
    width: 100%;
    margin: 0;
    padding: 0;
    -webkit-margin-before: 0;
    -webkit-margin-after: 0;
    -webkit-margin-start: 0;
    -webkit-margin-end: 0;
    -webkit-padding-start: 0;
}
ul.apps-list.search-apps {
    img.search-preloader {
        width: 100%;
        height: 100%;
        max-width: 73px;
        max-height: 73px;
        margin: auto;
        position: fixed;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
    }
}
ul.apps-list.filter:empty:after,
ul.apps-list.search-apps:empty:after{
    display: block;
    content: 'Sorry, no apps found.';
    padding: 15px;
    width: 100%;
    border: #ccc;
    font-size: large;
    text-align: center;
    clear: both;
}
