ul.apps-list.search-apps.hidden:empty:after {
    display: none;
}

ul.apps-list * {
    -webkit-user-select: none;
}

ul.apps-list li {
    display: block;
    flex: none;
    width: calc(var(--itemWidth));
    height: calc(var(--itemWidth));
    margin: 0em;
}

.app {
    position: relative;
    height: 100%;
    border-radius: 2px;
    cursor: pointer;
    border: 1px solid #fff;

    &:hover {
        background-color: #eee;
    }

    &:focus {
        outline: none;
        border: 1px solid #c9c9c9;
        background-color: #eee;
    }
}


.app_content {
    position: absolute;
    width: 100%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.app_name {
    margin: 0 auto;
}

.app_icon {
    display: block;
    margin: 0;
    padding: calc(var(--appIconPadding) * 1px);
    position: relative;
    width: calc(var(--itemWidth));
    height: calc(var(--itemWidth) - var(--itemNameHeight));
    background-position: center center;
    background-origin: content-box;
    background-repeat: no-repeat;
    background-size: contain;
}

.app_name {
    display: none;
    padding: 0.3em;
    font-size: 0.8rem;
    font-family: Roboto;
    text-align: center;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
}

.showAppNames .app .app_name {
    display: block;
}

.hasSearch .search {
    display: block;
}

.hasSearch .apps-list.search-apps {
    // position: relative;
    // top: 40px;
    // padding-bottom: 56px;
}
.list-container {
    flex-grow: 1;
    max-height: 220px;
    min-height: var(--containerHeight);
    //min-height: 75px;
    overflow-y: auto;
    &::-webkit-scrollbar {
        width: calc(var(--scrollWidth) - 2px);
    }
    &::-webkit-scrollbar-thumb{
        background-color: #bbb;
        border-radius: 3px;
    }
}
.hasSearch .apps-list {


    // position: relative;
    // top: 40px;
}



.search {
    display: none;
    position: relative;
    // position: fixed;
    // top: 0;
    // left: 0;
    height: 50px;
    padding: 5px;
    width: 100%;
    z-index: 1;
    background-color: #fff;

    .hint {
        opacity: 0;
        pointer-events: none;
    }

    .search_input {
        // margin: 0 auto;
        font-family: HindRegular;
        font-weight: lighter;
        display: block;
        width: 95%;
        height: 40px;
        padding: 5px;
        font-size: 16px;
        border: none;
        border-bottom: 1px solid #edf0f1;

        &:focus {
            outline: none;
            border-bottom-color: #c9c9c9;

        }

        &:focus + .hint {
            display: block;
            position: absolute;
            content: '';
            padding: 10px;
            bottom: 0;
            right: 0;
            color: gray;
            font-size: 10px;
            margin-right: 15px;
            transition: opacity 0.3s;
            opacity: 1;
        }
    }

}

.search-div {
    position: relative;
    // position: fixed;
    // bottom: 0;
    // left: 0;
    // right: 0;
    width: 100%;
    //margin: 0 auto;
    padding-top: 20px;
    // padding-bottom: 20px;
    background: #fff;
    .search-img {
        left: 17px;
        width: 18px;
        height: 18px;
        background: url("../images/search_icon.svg");
    }
    .search-close {
        right: 14px;
        width: 14px;
        height: 14px;
        background: url("../images/delete_icon.svg");
    }
    .search-img, .search-close {
        position: absolute;
        top: 67%;
        background-size: cover;
        transform: translateY(-50%);
    }
    .search-img:hover, .search-close:hover {
        cursor: pointer;
    }
    input.downloader {
        position: relative;
        width: 100%;
        padding: 10px 27px 8px 44px;
        border: 1px solid transparent;
        border-radius: 5px;
        font-family: HindRegular, sans-serif;
        font-weight: lighter;
        font-size: 16px;
        line-height: 1;
        background-color: #edf0f1;
        color: #656767;
        transition: all .3s ease;
    }
    input.downloader:focus {
        border: 1px solid #f3f3f3;
        box-shadow: 0px 2px 3px 1px rgba(0, 0, 0, 0.25); 
        outline: none;
        background: #fff;
        color: #000;
    }
}

.apps-list.search-apps {
    .app-link {
        position: relative;
        color: rgb(64, 64, 64);
        &:visited, &:hover, &:active, &:link {
            color: rgb(64, 64, 64);
        }
    }
    // .app-link::after {
    //     content:'';
    //     position: absolute;
    //     right: -96px;
    //     bottom: 32px;
    //     width: 32px;
    //     height: 32px;
    //     //background: url(../images/add_icon.svg);
    // }
    .app-icon {
        position: relative;
    }
    .add-icon {
        // width: calc(var(--itemWidth)*1px - 40%);
        // height: calc(var(--itemWidth)*1px - 40%);
        //width: calc()
        width: 40%;
        height: 40%;
        position: absolute;
        // bottom: calc(var(--appIconPadding)*1px - 10px);
        // right: calc(var(--appIconPadding)*1px - 10px);
        bottom: calc(var(--appIconPadding) * 1px);
        right: calc(var(--appIconPadding) * 1px);
        background-image: url(../images/add_icon.svg);
        background-repeat: no-repeat;
        background-size: contain;
        background-position: center center;
    }
}
